import React from 'react';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import Menu from './Menu';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';
import FixedContainer from './FixedContainer';
import throttle from 'lodash.throttle';

const Wrapper = styled.header`
  background: rgba(255, 255, 255, 0.97);
  box-shadow: 0 4px 12px 0 rgba(0,0,0,.05) !important;
  transition: top 0.2s ease-in-out;
  padding: .8rem 1rem;
  height: 4rem;
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  z-index: 9; 
`;

const delta = 5;
const navbarHeight = 64;
console.log('[dev:hugo] navbarHeight', navbarHeight);

class Header extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      didScroll: true,
      lastScrollTop: 0,
      hidden: false
    };
  };

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  };

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  };

  hideHeader = () => {
    console.log('[dev:hugo] hideHeader', );
    this.setState({
      hidden: true
    });
  };

  showHeader = () => {
    console.log('[dev:hugo] showHeader', );
    this.setState({
      hidden: true
    });
  };

  hasScrolled = throttle(() => {
    const st = window.scrollY;
    console.log('[dev:hugo] window.scrollY', window.scrollY);
    if (Math.abs(this.state.lastScrollTop - st) <= delta) { return; }
    if (st > this.state.lastScrollTop && st > navbarHeight) {
      this.hideHeader();
    } else {
      if(st < this.getDocHeight()) {
        this.showHeader();
      }
    }
    this.setState({
      lastScrollTop: st
    });
  }, 250);

  getDocHeight = () => {
    return Math.max(
      document.body.scrollHeight, document.documentElement.scrollHeight,
      document.body.offsetHeight, document.documentElement.offsetHeight,
      document.body.clientHeight, document.documentElement.clientHeight
    );
  };

  handleScroll = () => {
    this.setState({
      didScroll: true
    });
    this.hasScrolled();
  };

  render() {
    const { menu, url } = this.props;
    return (
      <Wrapper>
        <FixedContainer>
          <FormattedMessage id="title">
            {(txt) => (
              <Helmet
                defaultTitle={txt}
                titleTemplate={`%s | ${txt}`}
              />
            )}
          </FormattedMessage>
          <Menu menu={menu} url={url} />
        </FixedContainer>
      </Wrapper>
    );
  } 
};

Header.propTypes = {
  menu: PropTypes.array.isRequired,
  url: PropTypes.string
};

export default Header;
